import * as React from "react"
import { Helmet } from "react-helmet"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"
import PostList from "../components/post-list"
import IntroFrame from "../components/intro-frame"

export const query = graphql`
  query HomeQuery {
    site {
      siteMetadata {
        title
        description
        siteUrl
        author {
          name
        }
        social {
          twitterCreator
          fbAppID
        }
      }
    }
    file(name: { eq: "profile-picture" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
  }
`

export default function Home({ data, location }) {
  const { title, description, siteUrl, author, social } = data.site.siteMetadata
  const pageUrl = siteUrl + location.pathname
  const imageUrl =
    siteUrl + data.file.childImageSharp.gatsbyImageData.images.fallback.src

  return (
    <Layout>
      <Seo
        title={`${title} • Home`}
        defaultTitle={title}
        description={description}
        siteUrl={siteUrl}
        pageUrl={pageUrl}
        author={author.name}
        imageUrl={imageUrl}
        imageAlt="Filippo Bovo's profile picture."
        fbAppID={social.fbAppID}
        twitterCreator={social.twitterCreator}
      />
      <Helmet>
        <meta
          name="google-site-verification"
          content="DbcpUdIo8Gwc14-A-AzATDrM3OEew9qZHLUE6BEtiss"
        />
      </Helmet>
      <IntroFrame />
      <PostList />
    </Layout>
  )
}
